.footer{
    background-color: var(--container-color);
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.footer__container{
    padding: 2rem 0;
}
.footer__title,
.footer__link{
    color: var(--title-color);
}
.footer__title{
    text-align: center;
    margin-bottom: var(--mb-2);
}
.footer__link:hover{
    color: var(--title-color-dark);
}
.footer__list{
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-2);
}
.footer__social{
    display: flex;
    justify-content: center;
    column-gap: 1.25rem;
}
.footer__social-link{
    background-color: var(--title-color);
    color: var(--container-color);
    font-size: 1.125rem;
    padding: 0.4rem;
    border-radius: 0.5rem;
    display: inline-flex;
}
.footer__social-link:hover{
    background-color: var(--title-color-dark);
}
.footer__copy{
    display: block;
    margin-top: 4rem;
    color: var(--title-color);
    text-align: center;
    font-size: var(--smaller-font-size);
}

/* Dark Mode */
#dark .footer{
    background-color: hsl(251, 20%, 16%);
}
#dark .footer__title{
    color: #BCC5CE;
}
#dark .footer__link{
    color: #BCC5CE;
}
#dark .footer__copy{
    color: #bcc5cec4;
}
#dark .footer__social-link:hover{
    background-color: #ae8cfa;
}
#dark .footer__social-link{
    background-color: #ae8cfa;
}
/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .footer__social-link{
        padding: 0.25rem;
        border-radius: 0.25rem;
        font-size: 1rem;
    }
}